<template>
  <div id="bottom">
    <md-bottom-bar class="bottom">
      <h1>Total Cost: ${{ cost }}</h1>
    </md-bottom-bar>
  </div>
</template>

<script>
export default {
  name: 'bottom',
  props: ['cost']
}
</script>

<style>
.bottom {
  position: fixed;
  bottom: 0px;
  z-index: 1;
}
</style>



