<template>
  <div class="model3">
    
    <!-- Logo -->
    <div width="100%">
      <img class="logo" src="../assets/model3.png">
    </div>
    <md-layout md-align="center" md-gutter="16">
      <md-layout md-flex="35">
        <md-card>
        <md-card-media>
          <img src="../assets/battery-s.jpg" alt="People">
        </md-card-media>
        <md-card-header>
          <div class="md-title">Standard Battery</div>
        </md-card-header>
        <md-card-content>
          <md-list>
            <md-list-item><span>220 mile range</span></md-list-item>
            <md-list-item><span>0 to 60 mph in 5.6 seconds</span></md-list-item>
            <md-list-item><span>130 mph top speed</span></md-list-item>
            <md-list-item><span>130 miles of range in 30 minutes using Tesla's global Supercharger network</span></md-list-item>
          </md-list>
          <md-radio v-model="battery" md-value="1"></md-radio>
        </md-card-content>
      </md-card>
      </md-layout>

      <md-layout md-flex="35">
        <md-card>
        <md-card-media>
          <img src="../assets/battery-l.jpg" alt="People">
        </md-card-media>
        <md-card-header>
          <div class="md-title">Long Range Battery</div>
        </md-card-header>
        <md-card-content>
          <md-list>
            <md-list-item><span>310 mile range</span></md-list-item>
            <md-list-item><span>0 to 60 mph in 5.1 seconds</span></md-list-item>
            <md-list-item><span>140 mph top speed</span></md-list-item>
            <md-list-item><span>170 miles of range in 30 minutes using Tesla's global Supercharger network</span></md-list-item>
          </md-list>
          <md-radio v-model="battery" md-value="2"></md-radio>
        </md-card-content>
      </md-card>
      </md-layout>
    </md-layout>

    <h1>Color</h1>
    <div>
      <md-radio v-model="color" md-value="1">Solid Black</md-radio>
      <md-radio v-model="color" md-value="2">Midnight Silver Metallic</md-radio>
      <md-radio v-model="color" md-value="3">Deep Blue Metallic</md-radio>
      <br />
      <md-radio v-model="color" md-value="4">Silver Metallic</md-radio>
      <md-radio v-model="color" md-value="5">Pearl White Multi-Coat</md-radio>
      <md-radio v-model="color" md-value="6">Red Multi-Coat</md-radio>
    </div>
    <h1>Wheels</h1>
    <div>
      <md-radio v-model="wheels" md-value="1">18" Aero Wheels</md-radio>
      <md-radio v-model="wheels" md-value="2">19" Sport Wheels</md-radio>
    </div>
    <h1>Options</h1>
      <md-layout md-align="center" md-gutter="16">
      <md-layout md-flex="35">
        <md-card>
        <md-card-media>
          <img src="../assets/premium.jpg">
        </md-card-media>
        <md-card-header>
          <div class="md-title">Premium Upgrades</div>
        </md-card-header>
        <md-card-content>
          <md-list>
            <md-list-item style="margin-bottom: 20px;"><span>Premium heated seating and cabin materials throughout, including open pore wood decor, glass roof and a USB port for every passenger</span></md-list-item>
            <md-list-item style="margin-bottom: 20px;"><span>12-way, power adjustable front seats, steering column and side mirrors, with custom driver profiles</span></md-list-item>
            <md-list-item><span>Premium audio system with more power, tweeters, surround speakers and subwoofer</span></md-list-item>
          </md-list>
          <md-checkbox id="prem" value="prem" v-model="prem"></md-checkbox>
        </md-card-content>
      </md-card>
      </md-layout>
      </md-layout>
      <br />
      <md-layout md-align="center" md-gutter="16">
      <md-layout md-flex="35" style="margin-bottom: 20px;">
        <md-card>
        <md-card-header>
          <div class="md-title">Enhanced Autopilot</div>
        </md-card-header>
        <md-card-content>
          <md-list>
            <md-list-item style="margin-bottom: 20px;"><span>Model 3 will match speed to traffic conditions, keep within a lane, automatically change lanes, transition from one freeway to another, exit the freeway and self-park at your destination.</span></md-list-item>
            <md-list-item><span>Additional features will roll out over time through software updates.</span></md-list-item>
          </md-list>
          <md-checkbox id="eap" value="eap" v-model="eap"></md-checkbox>
        </md-card-content>
      </md-card>
      </md-layout>
      </md-layout>
      <md-layout md-align="center" md-gutter="16">
      <md-layout md-flex="35">
        <md-card>
        <md-card-header>
          <div class="md-title">Full Self-Driving Capability</div>
        </md-card-header>
        <md-card-content>
          <md-list>
            <md-list-item style="margin-bottom: 20px;"><span>In the future, Model 3 will be capable of conducting trips with no action required by the person in the driverâ€™s seat.</span></md-list-item>
            <md-list-item><span>This feature is dependent upon extensive software validation and regulatory approval, which may vary by jurisdiction.</span></md-list-item>
          </md-list>
          <md-checkbox id="fsd" value="fsd" v-model="fsd" :disabled="isNotFilled"></md-checkbox>
        </md-card-content>
      </md-card>
      </md-layout>
    </md-layout>
    <h1>Currency</h1>
    <div>
      <md-radio v-model="currency" md-value="hkd">HKD</md-radio>
      <md-radio v-model="currency" md-value="use">USD</md-radio>
    </div>
    <bottom :cost="cost"></bottom>
    </div>
</template>

<script>
export default {
  name: 'model3',
  data: () => ({
    battery: 1,
    color: 1,
    wheels: 1,
    prem: false,
    eap: false,
    fsd: false,
    currency: 'hkd'
  }),
  computed: {
    cost: function () {
      if (this.currency === 'hkd') {
        return this.hkd
      } else {
        return this.usd
      }
    },
    usd: function () {
      var batteryNum = 0
      if (this.battery > 1) { batteryNum = 9000 } else { batteryNum = 0 }
      var colorNum = 0
      if (this.color > 1) { colorNum = 1000 } else { colorNum = 0 }
      var wheelsNum = 0
      if (this.wheels > 1) { wheelsNum = 1500 } else { wheelsNum = 0 }
      var premNum = 0
      if (this.prem) { premNum = 5000 } else { premNum = 0 }
      var eapNum = 0
      if (this.eap) { eapNum = 5000 } else { eapNum = 0 }
      var fsdNum = 0
      if (this.fsd) { fsdNum = 3000 } else { fsdNum = 0 }
      var sum = 35000 + batteryNum + colorNum + wheelsNum + premNum + eapNum + fsdNum
      return sum
    },
    hkd: function () {
      var batteryNum = 0
      if (this.battery > 1) { batteryNum = 69900 } else { batteryNum = 0 }
      var colorNum = 0
      if (this.color > 1) { colorNum = 7800 } else { colorNum = 0 }
      var wheelsNum = 0
      if (this.wheels > 1) { wheelsNum = 11600 } else { wheelsNum = 0 }
      var premNum = 0
      if (this.prem) { premNum = 38800 } else { premNum = 0 }
      var eapNum = 0
      if (this.eap) { eapNum = 38800 } else { eapNum = 0 }
      var fsdNum = 0
      if (this.fsd) { fsdNum = 23300 } else { fsdNum = 0 }
      var sum = 290500 + batteryNum + colorNum + wheelsNum + premNum + eapNum + fsdNum
      var tax
      if (sum >= 500000) { tax = 150000 * 0.4 + 150000 * 0.75 + 200000 + (sum - 500000) * 1.15 } else if (sum >= 300000) { tax = 150000 * 0.4 + 150000 * 0.75 + (sum - 300000) } else if (sum >= 150000) { tax = 150000 * 0.4 + (sum - 150000) * 0.75 } else { tax = sum * 0.4 }
      if (tax >= 97500) { tax -= 97500 }
      return parseInt(sum + tax) + ' (Net $' + sum + ' + FRT $' + tax + ')'
    },
    isNotFilled: function () {
      if (!this.eap) { this.fsd = false }
      return !this.eap
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bottom-bar {
  margin-top: 100px
}

.logo {
  width: 50%
}

.battery {
  width: 40%;
  float: left;
  margin: 5%
}

.premium {
  width: 40%;
  float: left;
  margin: 5%
}
</style>



