<template>
    <div class="charger">
        <iframe src="https://www.google.com/maps/d/embed?mid=1VE3W7LpBMa51U8gwzRQyUM8IrxQ&hl=en_US" width="960%" height="960"></iframe>
    </div>
</template>

<script>
export default {
  name: 'chargers',
  data: () => ({
    battery: 1,
    color: 1,
    wheels: 1,
    prem: false,
    eap: false,
    fsd: false,
    currency: 'hkd'
  }),
  computed: {
    cost: function () {
      if (this.currency === 'hkd') {
        return this.hkd
      } else {
        return this.usd
      }
    },
    usd: function () {
      var batteryNum = 0
      if (this.battery > 1) { batteryNum = 9000 } else { batteryNum = 0 }
      var colorNum = 0
      if (this.color > 1) { colorNum = 1000 } else { colorNum = 0 }
      var wheelsNum = 0
      if (this.wheels > 1) { wheelsNum = 1500 } else { wheelsNum = 0 }
      var premNum = 0
      if (this.prem) { premNum = 5000 } else { premNum = 0 }
      var eapNum = 0
      if (this.eap) { eapNum = 5000 } else { eapNum = 0 }
      var fsdNum = 0
      if (this.fsd) { fsdNum = 3000 } else { fsdNum = 0 }
      var sum = 35000 + batteryNum + colorNum + wheelsNum + premNum + eapNum + fsdNum
      return sum
    },
    hkd: function () {
      var batteryNum = 0
      if (this.battery > 1) { batteryNum = 69900 } else { batteryNum = 0 }
      var colorNum = 0
      if (this.color > 1) { colorNum = 7800 } else { colorNum = 0 }
      var wheelsNum = 0
      if (this.wheels > 1) { wheelsNum = 11600 } else { wheelsNum = 0 }
      var premNum = 0
      if (this.prem) { premNum = 38800 } else { premNum = 0 }
      var eapNum = 0
      if (this.eap) { eapNum = 38800 } else { eapNum = 0 }
      var fsdNum = 0
      if (this.fsd) { fsdNum = 23300 } else { fsdNum = 0 }
      var sum = 290500 + batteryNum + colorNum + wheelsNum + premNum + eapNum + fsdNum
      var tax
      if (sum >= 500000) { tax = 150000 * 0.4 + 150000 * 0.75 + 200000 + (sum - 500000) * 1.15 } else if (sum >= 300000) { tax = 150000 * 0.4 + 150000 * 0.75 + (sum - 300000) } else if (sum >= 150000) { tax = 150000 * 0.4 + (sum - 150000) * 0.75 } else { tax = sum * 0.4 }
      if (tax >= 97500) { tax -= 97500 }
      return parseInt(sum + tax) + ' (Net $' + sum + ' + FRT $' + tax + ')'
    },
    isNotFilled: function () {
      if (!this.eap) { this.fsd = false }
      return !this.eap
    }
  }
}
</script>

