<template>
  <div id="common">
    <!-- Toolbar -->
    <md-toolbar class="md-accent">
      <md-button class="md-icon-button" href="https://www.youtube.com/channel/UCuQk1kqKYPHadq8CRlgAsoQ">
        <md-icon>video_library</md-icon>
      </md-button>
      <h2 class="md-title" style="flex: 1">Tesla Model 3 Configurator</h2>
      <md-button class="md-icon-button" id="custom" @click="openDialog('dialog1')">
        <md-icon>info</md-icon>
      </md-button>
    </md-toolbar>

    <!-- Dialog -->
    <md-dialog md-open-from="#custom" md-close-to="#custom" ref="dialog1">
      <md-dialog-title>Cost Structure</md-dialog-title>

      <md-dialog-content>
        <div><b>Model 3</b></div>
        <div>Base Model: $35,000 (HK$290,500)</div>
        <div>Long Range Battery: $9,000 (HK$69,900)</div>
        <div>Color: $1,000 (HK$7,800)</div>
        <div>Wheels: $1,500 (HK$11,600)</div>
        <div>Premium Package: $5,000 (HK$38,800)</div>
        <div>Enhanced Autopilot: $5,000 (HK$38,800)</div>
        <div>Full Self-Driving Capability: $3,000 (HK$23,300)</div>
        <hr>
        <div><b>Tax</b></div>
        <div>First HK$150,000: 40%</div>
        <div>Next HK$150,000: 75%</div>
        <div>Next HK$200,000: 100%</div>
        <div>Remaining: 115%</div>
        <div>Tax Waived: $97,500</div>
      </md-dialog-content>

      <md-dialog-actions>
        <md-button class="md-primary" @click="closeDialog('dialog1')">Ok</md-button>
      </md-dialog-actions>
    </md-dialog>
  </div>
</template>

<script>
export default {
  name: 'common',
  methods: {
    openDialog (ref) {
      this.$refs[ref].open()
    },
    closeDialog (ref) {
      this.$refs[ref].close()
    },
    onOpen () {
      console.log('Opened')
    },
    onClose (type) {
      console.log('Closed', type)
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-bottom: 75px
}
</style>



